            this.reward = 5000000;
            this.charges = '合同，运费不足500万时按500万计费，运费标准：';
            if (!this.fare_low_info_flag) {
              this.fare_low_info_flag = true;
              this.$notify.info({
                title: '最低运费限制',
                message: '体积运费不足500万时按500万计费！',
                onClose: this.fare_low_info_closed
              });
            }
          }
          this.reward = this.reward + this.extend_fee;
          this.reward = (this.reward.toFixed() + '').replace(/(\d{1,3})(?=(\d{3})+(?:$|\.))/g,'$1,');
          this.charges = this.contract_type_cn + this.charges + this.charges_uprice;
        } else {
          this.reward = '--';
          this.charges = '--';
        }
      },
      m3_notify_closed: function() {
        this.m3_error_flag = false;
      },
      collateral_info_closed: function() {
        this.collateral_info_flag = false;
      },
      no_routing_warning_closed: function() {
        this.no_routing_warning_flag = false;
      },
      fare_low_info_closed: function() {
        this.fare_low_info_flag = false;
      },
      showCollateredInfoNotify: function (val) {
        if (!this.collateral_info_flag && val > 5000000) {
          this.collateral_info_flag = true;
          this.$notify.info({
            title: '高保证金低体积运费',
            message: '体积运费不足保证金1%，运费按保证金2%计费！',
            onClose: this.collateral_info_closed
          });
        }
        this.charges = '合同（低体积高保证金——体积运费不足保证金1%），运费标准：';
        this.charges_uprice = '保证金的2%'
        this.extend_fee = 0;
      }
      
    }
  }
  
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #262626;
  margin-top: 60px;
}
.contract_font {
    background-color: #B3C0D1;
    color: #333;
    letter-spacing:10px;
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    line-height: 60px;
  }
.el-header {
    background-color: #B3C0D1;
    color: #333;
    letter-spacing:10px;
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    line-height: 60px;
  }
  .el-main, .el-footer {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 360px;
  }
  .el-row {
    margin-bottom: 1px;
    line-height: 40px;
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
  .el-select {
    width: 500px;
  }
  .el-input {
    width: 500px;
  }
</style>



